import PlayArrowRoundedIcon from '@material-ui/icons/PlayArrowRounded'
import PauseRoundedIcon from '@material-ui/icons/PauseRounded'
import { IconButton } from '@material-ui/core'
import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { setPlaying, setCurrentTime } from '../playerSlice'
import { RootState } from '../../../app/reducer'
import IconButtonProps from './types'

const PlayButton = ({
  isMobile = false,
  size = 'default',
}: IconButtonProps) => {
  const dispatch = useDispatch()
  const isPlaying = useSelector((state: RootState) => state.player.isPlaying)
  const onPlay = () => {
    // dispatch(se)
    dispatch(setPlaying(!isPlaying))
    if (playerRef) {
      dispatch(setCurrentTime(playerRef.currentTime))
    }
  }

  let [playerRef, setPlayerRef] = useState<HTMLAudioElement | null>(null)
  useEffect(() => {
    const player = document.getElementById('player')
    if (player) {
      // @ts-ignore
      setPlayerRef(player)
    }
    // eslint-disable-next-line
  }, [playerRef])

  let fontSize = '3rem'
  if (size === 'large') {
    fontSize = '5rem'
  }

  return (
    <IconButton aria-label="play/pause" style={{ padding: 0 }} onClick={onPlay}>
      {isPlaying ? (
        <PauseRoundedIcon fontSize={'large'} style={{ fontSize: fontSize }} />
      ) : (
        <PlayArrowRoundedIcon
          fontSize={'large'}
          style={{ fontSize: fontSize }}
        />
      )}
    </IconButton>
  )
}

export default PlayButton
