import React from 'react'
import { Layout } from 'antd'
import { Grid, IconButton, Typography } from '@material-ui/core'
import KeyboardArrowDownRoundedIcon from '@material-ui/icons/KeyboardArrowDownRounded'
import FullscreenRoundedIcon from '@material-ui/icons/FullscreenRounded'
import MoreVertRoundedIcon from '@material-ui/icons/MoreVertRounded'
import { useHistory } from 'react-router-dom'
import { RootState } from '../../../app/reducer'
import { useSelector } from 'react-redux'
import PrevButton from '../buttons/PrevButton'
import PlayButton from '../buttons/PlayButton'
import NextButton from '../buttons/NextButton'
import DesktopPlayerControls, {
  ControlButtons,
  TimeControl,
} from '../playerDefault/desktopPlayer/DesktopPlayerControls'
import ShuffleButton from '../buttons/ShuffleButton'
import LoopButton from '../buttons/LoopButton'

const PlayerFullScreenLayout = () => {
  const currentAudio = useSelector(
    (state: RootState) => state.player.currentAudio
  )

  return (
    <Layout
      style={{
        height: '100%',
        minHeight: '-webkit-fill-available',
        maxHeight: '-webkit-fill-available',
        color: 'white',
      }}
    >
      <PlayerHeader />
      <PlayerContent
        imageSrc={currentAudio.thumbnail}
        title={currentAudio.name}
      />
      <PlayerFooter />
    </Layout>
  )
}

const PlayerHeader = () => {
  const playlistTitle = useSelector(
    (state: RootState) => state.player.playlist.name
  )

  return (
    <Layout.Header
      className={'dev'}
      style={{ display: 'flex', padding: 0, justifyContent: 'space-between' }}
    >
      <BackButton />
      <div style={{ fontSize: '1rem' }}>{playlistTitle}</div>
      <div>
        <IconButton>
          <FullscreenRoundedIcon fontSize={'large'} />
        </IconButton>
        <IconButton>
          <MoreVertRoundedIcon fontSize={'large'} />
        </IconButton>
      </div>
    </Layout.Header>
  )
}
type PlayerContentProps = {
  imageSrc: string,
  title: string,
}
const PlayerContent = ({ imageSrc, title }: PlayerContentProps) => {
  return (
    <Layout.Content className={'dev'}>
      <div className={'dev'} style={{ height: 'calc(100% - 120px)' }}>
        <img
          src={imageSrc}
          style={{ objectFit: 'cover', maxWidth: '100%', maxHeight: '100%' }}
        />
      </div>
      <div className={'dev'} style={{ height: '120px' }}>
        <Typography variant={'h6'} color={'primary'}>
          {title}
        </Typography>
      </div>
    </Layout.Content>
  )
}

const PlayerFooter = () => {
  return (
    <Layout.Footer className={'dev'} style={{ padding: 0 }}>
      <div
        className={'dev'}
        style={{ height: '60px', paddingLeft: '20px', paddingRight: '20px' }}
      >
        <TimeControl />
      </div>
      <div className={'dev'} style={{ height: '140px' }}>
        <Grid
          item
          container
          justify={'space-around'}
          style={{ maxWidth: '320px' }}
          alignItems={'center'}
        >
          <ShuffleButton size={'large'} />
          <PrevButton size={'large'} />
          <PlayButton size={'large'} />
          <NextButton size={'large'} />
          <LoopButton size={'large'} />
        </Grid>
      </div>
    </Layout.Footer>
  )
}

export const BackButton = () => {
  const history = useHistory()
  const onBack = () => {
    if (history.length > 2) {
      history.goBack()
    } else {
      history.push('/')
    }
  }
  return (
    <IconButton onClick={onBack}>
      <KeyboardArrowDownRoundedIcon fontSize={'large'} />
    </IconButton>
  )
}

export default PlayerFullScreenLayout
