import React, { useEffect } from 'react'

import { useParams } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { search } from './searchSlice'
import { RootState } from '../../app/reducer'
import { Paper } from '@material-ui/core'
import { setAudio, setPlaylist } from '../player/playerSlice'
import { Audio, Playlist } from '../player/types'

const SearchContent = () => {
  const { q } = useParams()
  const dispatch = useDispatch()
  const searchResultInput = useSelector(
    (state: RootState) => state.search.searchResult.searchInput
  )

  useEffect(() => {
    if(decodeURIComponent(q) !== searchResultInput)
    dispatch(search(decodeURIComponent(q)))
  }, [q,searchResultInput])

  const searchResult = useSelector(
    (state: RootState) => state.search.searchResult,
  )

  const currentPlaylistId = useSelector(
    (state: RootState) => state.player.playlist.id,
  )

  const onAudioClick = (audio: Audio) => {
    if (currentPlaylistId !== `/search/${q}`) {
      const newPlaylist: Playlist = {
        id: `/search/${q}`,
        url: `/search/${q}`,
        name: 'search playlist',
        audios: searchResult.searchArr as Audio[],
      }
      dispatch(setPlaylist({ playlist: newPlaylist, audio }))
    } else {
      dispatch(setAudio(audio as Audio))
    }
  }

  return (
    <div>
      {searchResult.searchArr.map((el) => {
        let httpsThumbnail = el.thumbnail.replace('http:', 'https:')
        if (el.thumbnail.substr(0, 2) === '//') {
          httpsThumbnail = `https:${el.thumbnail}`
        }

        return (
          <Paper
            style={{
              textAlign: 'left',
              margin: '5px',
              display: 'flex',
              alignItems: 'center',
              maxWidth: '1955px',
              overflow: 'hidden',
            }}
            key={el.id}
            onClick={() => {
              onAudioClick(el as Audio)
            }}
          >
            <img
              style={{ objectFit: 'cover' }}
              src={httpsThumbnail}
              width={100}
              height={64}
            />
            <div style={{ display: 'flex', flexWrap: 'wrap', overflow: 'hidden' }}>
              <div style={{
                paddingLeft: '5px',
                textOverflow: 'ellipsis',
                whiteSpace: 'nowrap',
                overflow: 'hidden',
                width: '100%',
              }}>{el.name}</div>
              <div style={{
                paddingLeft: '5px',
                textOverflow: 'ellipsis',
                whiteSpace: 'nowrap',
                overflow: 'hidden',
                width: '100%',
                fontSize: '12px',
                lineHeight: '16px',
                letterSpacing: '0.015em',
                color: '#d9d8d8',
              }}>{(el as Audio).artist}</div>
            </div>
          </Paper>
        )
      })}
    </div>
  )
}
export default SearchContent
