import React from 'react'
import { Grid } from '@material-ui/core'
import styles from './MobilePlayerInfo.module.css'
import { RootState } from '../../../../app/reducer'
import { useSelector } from 'react-redux'
import { Link } from 'react-router-dom'

const MobilePlayerInfo = () => {
  const currentAudio = useSelector(
    (state: RootState) => state.player.currentAudio
  )
  return (
    <Grid
      className={styles.Info}
      container
      direction="row"
      justify="flex-start"
      alignItems="center"
    >
      <Link to={'/player'}>
        <img
          src={currentAudio.thumbnail}
          alt={currentAudio.name}
          height={48}
          width={80}
          className={styles.Image}
        />
      </Link>
      <Grid item className={styles.Meta}>
        <div className={styles.SongName}>{currentAudio.name}</div>
        <div className={styles.Artist}>{currentAudio.artist}</div>
      </Grid>
    </Grid>
  )
}

export default MobilePlayerInfo
